body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.g-relic {
  margin: 20px;
  position:relative;

}

.g-relic-image{
  border:solid 20px  white;
  border-bottom:solid 100px  white;
  width: auto;
  height: 480px;
  box-shadow: 0px 0px 3px 1.5px #ccc;
}

.g-relic-info{
  position: absolute;
  bottom:50px;
  left:20px;
  margin-top: 10px;
  height: 30px;
}

.g-relic-title{
  font-size: 20px;
  margin: 0;
}

.g-relic-year{
  font-weight: lighter;
  font-size: 15px;
  color: #7abaff;
  margin:0;
}

.success-msg{
    color: #57A773;
    padding: 10px 15px;
}

.err-msg{
    color: #EE6352;
    padding: 10px 15px;
}

.footer-zitat{
  text-align: center;
  margin:0;
}

.footer-year{
  text-align: right;
}

@media only screen and (min-width: 375px) {
  .g-relic-image{
    width: 320px;
    height: auto;
  }
}

@media only screen and (min-width: 640px) {
  .g-relic-image{
    height: auto;
    width:400px;
  }
}

@media only screen and (min-width: 1024px) {
  .g-relic-image{
    height: auto;
    width:600px;
  }

  .g-relic {
    position:relative;
  }

  .g-relic-info{
    position: absolute;
    top:10px;
    left:20px;
    margin-top:10px;
    height: 30px;
  }

  .g-relic-image{
    border:solid 20px  white;
    border-top:solid 100px  white;
    box-shadow: 0px 0px 3px 1.5px #ccc;
  }
}



